import React from "react";
import { useHistory } from 'react-router-dom';

function Header() {
    const history = useHistory();

    function handleLogoClick() {
        history.push("/");
    }

    function handleFireLogoClick() {
        history.push("/nations/fire");
    }

    function handleAirLogoClick() {
        history.push("/nations/air");
    }

    function handleWaterLogoClick() {
        history.push("/nations/water");
    }

    function handleEarthLogoClick() {
        history.push("/nations/earth");
    }

    return(
        <header className="header">
            <img onClick={handleFireLogoClick} className="headerPics"
                alt="Fire Nation Symbol"
                src="data:image/jpeg;base64,"
            />
            <img onClick={handleAirLogoClick} className="headerPics"
                alt="Air Nation Symbol"
                src="data:image/jpeg;base64,"
            />
            <img className="headerPics"
                onClick={handleLogoClick}
                id="logo"
                src="https://www.seekpng.com/png/full/326-3266854_avatar-the-last-airbender-logo-avatar-the-last.png"
            />
            <img onClick={handleWaterLogoClick} className="headerPics"
                alt="Water Nation Symbol"
                src="data:image/jpeg;base64,"
            />
            <img onClick={handleEarthLogoClick} className="headerPics"
                alt="Earth Nation Symbol"
                src="data:image/jpeg;base64,"
            />
        </header>
    );
}

export default Header;